.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  position: fixed;
  left: 0;
  top: 5rem;
  width: 15rem;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0.96;
  height: 100vh;
  font-size: 1.5rem;
  a {
    text-decoration: none;
    padding: 1rem 0;
    color: grey;
  }
  a:hover {
    color: #fff;
    font-weight: 600;
  }
}
